import React from 'react';
import Colorizer from './Colorizer';
import './App.css';

function App() {
  return (
    <div className="App">
      <h1>Colorizer</h1>
      <h2>Click the Button To Change the Color!</h2>
      <Colorizer />
    </div>
  );
}

export default App;



